<template>
    <div id="printer">
        <div class="btn-group">
        	<router-link :to="{path:'/printer/edit'}">
        		<button type="button" class="add">添加</button>
        	</router-link>
        	<button type="button" class="del">删除</button>
        </div>
        <div class="printer-table">
        	<el-table border style="width: 100%" stripe :data='printList'>
				<el-table-column type="selection" min-width="52">
				</el-table-column>
				<el-table-column label="打印机ID" align="center"  min-width="180">
					 <template slot-scope="scope">
                        <span>{{ scope.row.printer_id}}</span>
                    </template>
				</el-table-column>
				<el-table-column label="名称" align="center"  min-width="180">
					 <template slot-scope="scope">
                        <span>{{ scope.row.printer_name}}</span>
                    </template>
				</el-table-column>
				<el-table-column label="支持单据" align="justify" min-width="300">
					 <template slot-scope="scope">
                        <span v-for='(item,index) in scope.row.documents_type'>{{docType[item]}}<i v-if="index < scope.row.documents_type.length-1"> / </i></span>
                    </template>
				</el-table-column>
				<el-table-column label="打印商品分类" align="center" min-width="250">
				</el-table-column>
				<el-table-column  label="规格" align="center" min-width="180">
					 <template slot-scope="scope">
                        <span>{{ scope.row.printer_spec}}mm</span>
                    </template>
				</el-table-column>
				<el-table-column label="型号" align="center" min-width="180">
					 <template slot-scope="scope">
                        <span>{{ scope.row.printer_model}}</span>
                    </template>
				</el-table-column>
				<el-table-column label="备注" align="center" min-width="180">
					 <template slot-scope="scope">
                        <span>{{ scope.row.remark}}</span>
                    </template>
				</el-table-column>
				<el-table-column label="操作" min-width="180" align="center">
					<template slot-scope="scope">
						<router-link :to="{path:'/printer/edit',query:{id:scope.row.printer_id}}">
							<span size="small" type="text" class="blue">编辑
							</span>
						</router-link>
						<span size="small" type="text" class="red">删除
						</span>
					</template>
				</el-table-column>
			</el-table>
			 <!-- 分页 starat -->
            <div class="pagination-container">
                <el-pagination 
                @size-change="handleSizeChange" 
                @current-change="handleCurrentChange" 
                :current-page.sync="listQuery.page" 
                :page-sizes="[10,20,40]" 
                :page-size="listQuery.limit" 
                layout="sizes, jumper, prev, pager, next" 
                :total="total">
                </el-pagination>
            </div>
            <!-- 分页 end -->
        </div>
    </div>
</template>

<script>
	import { documentsType } from '@/config/cfg';
	import { getPrinter } from '../api';
export default{
	data(){
		return{
			printList:[],
			docType:'',
			 //分页
	      total: null, //分页总条数
	      listQuery: {
	        page: 1,
	        limit: 10,
	        importance: undefined,
	        title: undefined
	      }
		}
	},
	created(){
		this.getData();
	},
	computed:{
			documentsType(){
				return documentsType;
			}
		},
	methods:{
		getData(){
			let _this = this;
			getPrinter({
				
			},function(resp){
				_this.printList = resp.data.printer;
				_this.docType = documentsType.code;
			});
		},
		 //分页事件处理，条数和当前页数改变触发
    handleSizeChange(val) {
      this.listQuery.limit = val;
      this.getData();
    },
    //目标页
    handleCurrentChange(val) {
      this.listQuery.page = val;
      this.getData();
    }
	}
};
</script>

<style lang="scss" scoped>
    @import 'src/styles/mixin.scss';
    .btn-group{
    	padding: 20px 0;
    	background: #fff;
    	text-align: right;
    	button{
    		cursor: pointer;
    	}
    	.add{
    		@include wh(100px,34px);
    		@include fc(14px,#4877E7);
    		background:#fff;
    		border:1px solid #4877e7;
    		margin-right:9px;
    	}
    	.del{
    		@include wh(100px, 34px);
    		@include fc(14px,#E7487E);
            border: 1px solid #E7487E;
            background: #fff;
    	}
	   
    }
     .blue {
			font-size: 14px;
			color: #4877E7;
			margin-right:6px;
		}
		
		.red {
			font-size: 14px;
			color: #E7487E;
		}
		.printer-table{
			background: #fff;
			height: 100%;
		}
</style> 
<style lang="scss">
	@import 'src/styles/mixin.scss';
	#printer{
		.el-table tbody tr{
			height: 80px;
		}
		.el-table .cell, .el-table th>div{
			padding: 0 8px 0 8px;
		}
		.pagination-container {
		  text-align: center;
		  padding: 50px 14px 6px;
		}
	}
</style>


